import React from 'react';
import PropTypes from 'prop-types';

class DropDown extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			isOpen: false,
			value: '',
		};
	}

	onOptionClick(value) {
		this.setState({
			isOpen: false,
			value,
		});
		this.props.onChange(value);
	}

	onInputClick() {
		this.setState({
			isOpen: !this.state.isOpen,
		});
	}

	render() {
		const options = this.props.options.map((option) => <li key={option.name} onClick={(e) => this.onOptionClick(e)}>{option.name}</li>);
		return (
			<div className={'dropdown ' + (this.state.isOpen ? 'is-open' : '')}>
				<input value={this.state.value} onClick={(e) => this.onInputClick(e)} />
				{ this.props.options.length && <ul>{options}</ul> }
			</div>
		);
	}
}

DropDown.propTypes = {
	options: PropTypes.array.isRequired,
	onChange: PropTypes.func.isRequired,
};

export default DropDown;
