import React from 'react';

const ProductCategoryRow = (props) => (
  (
  	<tr>
  	  <th colSpan="2">
  	    {props.category}
  	  </th>
  	</tr>
  )
);

const ProductRow = (props) => {
  const { product } = props;
  const name = product.stocked ? product.name : <span style={{color: 'red'}}>{product.name}</span>;
  return (
    <tr>
      <td>{name}</td>
      <td>{product.price}</td>
    </tr>
  );
};

const ProductTable = (props) => {
  const { filterText, inStockOnly, products } = props;
  const rows = [];
  let lastCategory = null;
  products.forEach((product) => {
    if (product.name.indexOf(filterText) === -1) {
      return;
    }
    if (inStockOnly && !product.stocked) {
      return;
    }
    if (product.category !== lastCategory) {
      rows.push(
        <ProductCategoryRow
          category={product.category}
          key={product.category} />
      );
    }
    rows.push(
      <ProductRow
        product={product}
        key={product.name}
      />
    );
    lastCategory = product.category;
  });
  return (
    <table>
      <thead>
        <tr>
          <th>Name</th>
          <th>Price</th>
        </tr>
      </thead>
      <tbody>{rows}</tbody>
    </table>
  );
};

class SearchBar extends React.Component {
  // constructor(props) {
  //   super(props);
  //   this.handleFilterTextChange = this.handleFilterTextChange.bind(this);
  //   this.handleInStockChange = this.handleInStockChange.bind(this);
  // }
  
  handleFilterTextChange(e) {
    this.props.onFilterTextChange(e.target.value);
  }
  
  handleInStockChange(e) {
    this.props.onInStockChange(e.target.checked);
  }
  
  render() {
    return (
      <form>
        <input
          type="text"
          placeholder="Search..."
          value={this.props.filterText}
          onChange={(e)=>this.handleFilterTextChange(e)}
        />
        <p>
          <input
            type="checkbox"
            checked={this.props.inStockOnly}
            onChange={(e)=>this.handleInStockChange(e)}
          />
          {' '}
          Only show products in stock
        </p>
      </form>
    );
  }
}

class FilterableProductTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filterText: '',
      inStockOnly: false
    };
    
    // this.handleFilterTextChange = this.handleFilterTextChange.bind(this);
    // this.handleInStockChange = this.handleInStockChange.bind(this);
  }

  handleFilterTextChange(filterText) {
    this.setState({
      filterText: filterText
    });
  }
  
  handleInStockChange(inStockOnly) {
    this.setState({
      inStockOnly: inStockOnly
    })
  }

  render() {
    return (
      <>
        <SearchBar
          filterText={this.state.filterText}
          inStockOnly={this.state.inStockOnly}
          onFilterTextChange={(e)=>this.handleFilterTextChange(e)}
          onInStockChange={(e)=>this.handleInStockChange(e)}
        />
        <ProductTable
          products={this.props.products}
          filterText={this.state.filterText}
          inStockOnly={this.state.inStockOnly}
        />
      </>
    );
  }
}

export default FilterableProductTable;
