import React from 'react';
import { Switch, Route } from 'react-router-dom';

import Home from './pages/example/Home';
import Clock from './pages/example/Clock';
import Game from './pages/example/Game';
import ExampleReducer from './pages/example/Example.reducer.js';
import FetchData from './pages/fetch/FetchData17.js';

const Main = () => (
  <main>
    <Switch>
      <Route exact path='/' component={Home}/>
      <Route path='/clock' component={Clock}/>
      <Route path='/game.html' component={Game}/>
      <Route path='/example' component={ExampleReducer}/>
      <Route path='/search' component={FetchData}/>
    </Switch>
  </main>
)

export default Main;